ol-span-12 lg:col-span-4\">\n                <div class=\"w-full intro-y box p-5\">\n                    <div class=\"flex flex-wrap -mx-3 mb-6\">\n\n                        <div class=\"w-full px-3 mb-4\">\n                            <label class=\"form-label\"\n                                   for=\"is_active\">\n                                Active Status\n                            </label>\n                            <div class=\"form-switch mt-2\">\n                                <input type=\"checkbox\"\n                                       id=\"is_active\"\n                                       class=\"form-check-input\"\n                                       :checked=\"data.is_active\"\n                                       @change=\"onCheckbox($event,'is_active')\"/>\n                            </div>\n                        </div>\n\n                        <div class=\"w-full px-3 mb-4\">\n                            <label class=\"form-label\"\n                                   for=\"featured_for_home\">\n                                Featured For Home\n                            </label>\n                            <div class=\"form-switch mt-2\">\n                                <input type=\"checkbox\"\n                                       id=\"featured_for_home\"\n                                       class=\"form-check-input\"\n                                       :checked=\"data.featured_for_home\"\n                                       @change=\"onCheckbox($event,'featured_for_home')\"/>\n                            </div>\n                            <small>The product will be added in Home</small>\n                        </div>\n                    </div>\n                </div>\n            </div>\n\n        </div>\n    </div>\n</template>\n\n<script>\nimport FileUpload from '../FileUpload'\n\nexport default {\n    name: \"Edit\",\n    components: {\n        FileUpload,\n    },\n    props: {\n        category: {\n            type: Object,\n            required: true\n        }\n    },\n    data: function () {\n        return {\n            data: this.category,\n            btnLoading: false\n        }\n    },\n    computed: {\n        slug: function () {\n            return this.sanitizeTitle(this.data.name);\n        }\n    },\n    methods: {\n        sanitizeTitle: function (title) {\n            if (!title) {\n                return \"\";\n            }\n            let slug = \"\";\n            // Change to lower case\n            const titleLower = title.toLowerCase();\n            // Letter \"e\"\n            slug = titleLower.replace(/e|||||||||||/gi, 'e');\n            // Letter \"a\"\n            slug = slug.replace(/a|||||||||||||||||/gi, 'a');\n            // Letter \"o\"\n            slug = slug.replace(/o|||||||||||||||||/gi, 'o');\n            // Letter \"u\"\n            slug = slug.replace(/u|||||||||||/gi, 'u');\n            // Letter \"d\"\n            slug = slug.replace(//gi, 'd');\n            // Trim the last whitespace\n            slug = slug.replace(/\\s*$/g, '');\n            // Change whitespace to \"-\"\n            slug = slug.replace(/\\s+/g, '-');\n\n            return slug;\n        },\n        setImage(img, key, index = null) {\n            if (index == null) {\n                this.data[key] = img\n            } else {\n                this.data.items[index][key] = img;\n            }\n        },\n        onSubmit() {\n            this.btnLoading = true;\n            axios.put('/market/category/' + this.data.id, this.data)\n                .then(() => {\n                    this.btnLoading = false;\n                    this.$alertify.success('Category updated successfully')\n\n                    setTimeout(() => {\n                        window.location.href = '/market/category'\n                    }, 1000);\n\n                }).catch((error) => {\n                this.btnLoading = false;\n                if (error.response.status === 422) {\n                    this.$alertify.error(Object.values(error.response.data.errors)[0][0])\n                }\n            })\n        },\n        onCheckbox(e, key) {\n            this.data[key] = e.target.checked;\n        }\n    }\n}\n</script>\n\n<style scoped>\n@media (min-width: 768px) {\n    .md\\:w-1\\/2 {\n        width: 25%;\n    }\n\n    .md\\:w-1\\/3 {\n        width: 33.333333%;\n    }\n\n    .md\\:w-1\\/4 {\n        width: 50%;\n    }\n\n    .md\\:w-1\\/5 {\n        width: 75%;\n    }\n}\n\n.border-radius-15 {\n    border-radius: 15px;\n}\n\n.close {\n    position: absolute;\n    right: 12px;\n    margin-top: -10px;\n}\n</style>\n"],"sourceRoot":""}]);
