     Slug\n                            </label>\n                            <div class=\"flex items-center\">\n                                <input\n                                    class=\"form-control w-full\"\n                                    id=\"slug\" placeholder=\"Slug is autogenerated\" type=\"text\"\n                                    readonly\n                                    v-model=\"slug\">\n                            </div>\n                        </div>\n\n                        <div class=\"w-full px-3 mb-4\">\n                            <label class=\"form-label\"\n                                   for=\"icon\">\n                                Browse Image\n                            </label>\n                            <FileUpload\n                                id=\"icon\"\n                                accept_file_types=\"image/jpeg, image/png, image/svg+xml\"\n                                label=\"Browse\"\n                                :my-files=\"blog.image\"\n                                max_file_size=\"1MB\"\n                                @process=\"setImage($event,'image')\"\n                            />\n                        </div>\n\n                        <div class=\"w-full px-3 mb-4\">\n                            <label class=\"form-label\"\n                                   for=\"image_alt\">\n                                Image alt\n                            </label>\n                            <div class=\"flex items-center\">\n                                <input\n                                    class=\"form-control w-full\"\n                                    id=\"image_alt\" placeholder=\"Enter Image alt\" type=\"text\"\n                                    v-model=\"data.image_alt\">\n                            </div>\n                        </div>\n\n                        <div class=\"w-full md:w-1/4 px-3 mb-4\">\n                            <label class=\"form-label\"\n                                   for=\"blog_category_id\">\n                                Select Category\n                            </label>\n                            <div class=\"flex items-center\">\n                                <v-select\n                                    id=\"blog_category_id\"\n                                    class=\"w-full\"\n                                    :reduce=\"options => options.id\"\n                                    :options=\"categories\"\n                                    label=\"name\"\n                                    placeholder=\"Select\"\n                                    v-model=\"data.blog_category_id\"\n                                ></v-select>\n                            </div>\n                        </div>\n\n                        <div class=\"w-full md:w-1/4 px-3 mb-4\">\n                            <label class=\"form-label\"\n                                   for=\"stores\">\n                                Select Stores\n                            </label>\n                            <div class=\"flex items-center\">\n                                <v-select\n                                    id=\"stores\"\n                                    class=\"w-full\"\n                                    :reduce=\"options => options.id\"\n                                    multiple\n                                    :options=\"stores\"\n                                    label=\"name\"\n                                    placeholder=\"Select\"\n                                    v-model=\"data.stores\"\n                                ></v-select>\n                            </div>\n                        </div>\n\n                        <div class=\"w-full px-3 mb-4\">\n                            <label class=\"form-label\"\n                                   for=\"short_description\">\n                                Short Description\n                            </label>\n                            <div class=\"flex items-center\">\n                                <textarea\n                                    id=\"short_description\" class=\"form-control w-full\"\n                                    v-model=\"data.short_description\"\n                                    cols=\"30\" rows=\"3\"\n                                ></textarea>\n                            </div>\n                        </div>\n\n                        <div class=\"w-full px-3 mb-4\">\n                            <label class=\"form-label\"\n                                   for=\"long_description\">\n                                Long Description\n                            </label>\n                            <ckeditor v-model=\"data.long_description\" id=\"long_description\"></ckeditor>\n                        </div>\n\n                    </div>\n                </div>\n\n                <div class=\"w-full intro-y box mt-4 p-5\">\n                    <div class=\"flex flex-wrap -mx-3 mb-6\">\n                        <div class=\"w-full px-3 mb-4\">\n                            <label class=\"form-label\"\n                                   for=\"meta_title\">\n                                Meta Title\n                            </label>\n                            <div class=\"flex items-center\">\n                                <input\n                                    class=\"form-control w-full\"\n                                    id=\"meta_title\" placeholder=\"Enter Meta Title\" type=\"text\"\n                                    v-model=\"data.meta_title\">\n                            </div>\n                        </div>\n\n                        <div class=\"w-full px-3 mb-4\">\n                            <label class=\"form-label\"\n                                   for=\"meta_keyword\">\n                                Meta Keywords\n                            </label>\n                            <div class=\"flex items-center\">\n                                <input\n                                    class=\"form-control w-full\"\n                                    id=\"meta_keyword\" placeholder=\"Enter Meta Keywords\" type=\"text\"\n                                    v-model=\"data.meta_keywords\">\n                            </div>\n                        </div>\n\n                        <div class=\"w-full px-3 mb-4\">\n                            <label class=\"form-label\"\n                                   for=\"meta_description\">\n                                Meta Description\n                            </label>\n                            <div class=\"flex items-center\">\n                                <textarea\n                                    id=\"meta_description\" class=\"form-control w-full\"\n                                    v-model=\"data.meta_description\"\n                                    cols=\"30\" rows=\"10\"\n                                ></textarea>\n                            </div>\n                        </div>\n\n                    </div>\n                </div>\n\n                <div class=\"text-right mt-5\">\n                    <a href=\"/advertisement/blog\"\n                       class=\"btn btn-outline-secondary w-24 mr-1\">Cancel</a>\n                    <button type=\"submit\"\n                            class=\"btn btn-primary w-24\"\n                            :disabled=\"btnLoading\"\n                            @click=\"onSubmit\">Update\n                    </button>\n                </div>\n            </div>\n\n            <div class=\"intro-y col-span-12 lg:col-span-4\">\n                <div class=\"w-full intro-y box p-5\">\n                    <div class=\"flex flex-wrap -mx-3 mb-6\">\n\n                        <div class=\"w-full px-3 mb-4\">\n                            <label class=\"form-label\"\n                                   for=\"is_active\">\n                                Active Status\n                            </label>\n                            <div class=\"form-switch mt-2\">\n                                <input type=\"checkbox\"\n                                       id=\"is_active\"\n                                       class=\"form-check-input\"\n                                       :checked=\"data.is_active\"\n                                       @change=\"onCheckbox($event,'is_active')\"/>\n                            </div>\n                        </div>\n\n                        <div class=\"w-full px-3 mb-4\">\n                            <label class=\"form-label\"\n                                   for=\"featured_for_home\">\n                                Featured For Home\n                            </label>\n                            <div class=\"form-switch mt-2\">\n                                <input type=\"checkbox\"\n                                       id=\"featured_for_home\"\n                                       class=\"form-check-input\"\n                                       :checked=\"data.featured_for_home\"\n                                       @change=\"onCheckbox($event,'featured_for_home')\"/>\n                            </div>\n                            <small>The blog will be added in feature for home</small>\n                        </div>\n\n                    </div>\n                </div>\n            </div>\n\n        </div>\n    </div>\n</template>\n\n<script>\nimport FileUpload from '../FileUpload'\n\nexport default {\n    name: \"Edit\",\n    props: ['stores', 'categories', 'blog'],\n    components: {\n        FileUpload,\n    },\n    data: function () {\n        return {\n            data: {\n                title: null,\n                image: null,\n                image_alt: null,\n                short_description: null,\n                long_description: null,\n                blog_category_id: null,\n                stores: [],\n                meta_title: null,\n                meta_keywords: null,\n                meta_description: null,\n                is_active: true,\n                featured_for_home: false\n            },\n            btnLoading: false\n        }\n    },\n    computed: {\n        slug: function () {\n            return this.sanitizeTitle(this.data.title);\n        }\n    },\n    created() {\n        this.mapKeys()\n    },\n    methods: {\n        sanitizeTitle: function (title) {\n            if (!title) {\n                return \"\";\n            }\n            let slug = \"\";\n            // Change to lower case\n            const titleLower = title.toLowerCase();\n            // Letter \"e\"\n            slug = titleLower.replace(/e|||||||||||/gi, 'e');\n            // Letter \"a\"\n            slug = slug.replace(/a|||||||||||||||||/gi, 'a');\n            // Letter \"o\"\n            slug = slug.replace(/o|||||||||||||||||/gi, 'o');\n            // Letter \"u\"\n            slug = slug.replace(/u|||||||||||/gi, 'u');\n            // Letter \"d\"\n            slug = slug.replace(//gi, 'd');\n            // Trim the last whitespace\n            slug = slug.replace(/\\s*$/g, '');\n            // Change whitespace to \"-\"\n            slug = slug.replace(/\\s+/g, '-');\n\n            return slug;\n        },\n        setImage(img, key, index = null) {\n            if (index == null) {\n                this.data[key] = img\n            } else {\n                this.data.items[index][key] = img;\n            }\n        },\n        mapKeys() {\n            for (let i of Object.keys(this.data)) {\n                if (i == \"stores\") {\n                    this.data.stores = this.blog.stores.map(e => e.id);\n                } else if (i in this.blog) {\n                    this.data[i] = this.blog[i];\n                }\n            }\n        },\n        onSubmit() {\n            this.btnLoading = true;\n            axios.put('/advertisement/blog/' + this.blog.id, this.data)\n                .then(() => {\n                    this.btnLoading = false;\n                    this.$alertify.success('Blog updated successfully')\n\n                    setTimeout(() => {\n                        window.location.href = '/advertisement/blog'\n                    }, 1000);\n\n                }).catch((error) => {\n                this.btnLoading = false;\n                if (error.response.status === 422) {\n                    this.$alertify.error(Object.values(error.response.data.errors)[0][0])\n                }\n            })\n        },\n        onCheckbox(e, key) {\n            this.data[key] = e.target.checked;\n        }\n    }\n}\n</script>\n\n<style scoped>\n@media (min-width: 768px) {\n    .md\\:w-1\\/2 {\n        width: 25%;\n    }\n\n    .md\\:w-1\\/3 {\n        width: 33.333333%;\n    }\n\n    .md\\:w-1\\/4 {\n        width: 50%;\n    }\n\n    .md\\:w-1\\/5 {\n        width: 75%;\n    }\n}\n\n.border-radius-15 {\n    border-radius: 15px;\n}\n\n.close {\n    position: absolute;\n    right: 12px;\n    margin-top: -10px;\n}\n</style>\n"],"sourceRoot":""}]);
