<template>
    <div>
        <div class="grid grid-cols-12 mt-8">
            <div class="col-span-12 lg:col-span-8 sm:col-span-12">
                <h2 class="text-lg font-medium truncate mr-5">
                    User Create
                </h2>
            </div>
        </div>
        <div class="grid grid-cols-12 gap-6 mt-5">
            <div class="intro-y col-span-12">
                <div class="w-full intro-y box p-5">
                    <div class="flex flex-wrap -mx-3 mb-6">
                        <div class="w-full md:w-1/3 px-3 mb-4">
                            <label class="form-label"
                                   for="first_name">
                                First Name
                            </label>
                            <div class="flex items-center">
                                <input
                                    class="form-control w-full"
                                    id="first_name" placeholder="Enter First Name" type="text"
                                    v-model="data.first_name">
                            </div>
                        </div>

                        <div class="w-full md:w-1/3 px-3 mb-4">
                            <label class="form-label"
                                   for="last_name">
                                Last Name
                            </label>
                            <div class="flex items-center">
                                <input
                                    class="form-control w-full"
                                    id="last_name" placeholder="Enter Last Name" type="text"
                                    v-model="data.last_name">
                            </div>
                        </div>

                        <div class="w-full md:w-1/3 px-3 mb-4">
                            <label class="form-label"
                                   for="username">
                                Username
                            </label>
                            <div class="flex items-center">
                                <input
                                    class="form-control w-full"
                                    autocomplete="off"
                                    id="username" placeholder="Enter Username" type="text"
                                    v-model="data.username">
                            </div>
                        </div>

                        <div class="w-full md:w-1/3 px-3 mb-4">
                            <label class="form-label"
                                   for="email">
                                Email
                            </label>
                            <div class="flex items-center">
                                <input
                                    class="form-control w-full"
                                    id="email" placeholder="Enter Email" type="email"
                                    v-model="data.email">
                            </div>
                        </div>

                        <div class="w-full md:w-1/3 px-3 mb-4">
                            <label class="form-label"
                                   for="password">
                                Password
                            </label>
                            <div class="flex items-center">
                                <input
                                    class="form-control w-full"
                                    id="password" placeholder="Enter Password" type="password"
                                    v-model="data.password">
                            </div>
                        </div>

                        <div class="w-full md:w-1/3 px-3 mb-4">
                            <label class="form-label"
                                   for="mobile_no">
                                Mobile No
                            </label>
                            <div class="flex items-center">
                                <input
                                    class="form-control w-full"
                                    id="mobile_no" placeholder="Enter Mobile No" type="text"
                                    v-model="data.mobile_no">
                            </div>
                        </div>

                        <div class="w-full md:w-1/3 px-3 mb-4">
                            <label class="form-label"
                                   for="network_id">
                                Select Network
                            </label>
                            <div class="flex items-center">
                                <v-select
                                    id="network_id"
                                    class="w-full"
                                    :reduce="options => options.id"
                                    :options="networks"
                                    label="name"
                                    placeholder="Select"
                                    v-model="data.network_id"
                                ></v-select>
                            </div>
                        </div>

                        <div class="w-full md:w-1/3 px-3 mb-4">
                            <label class="form-label"
                                   for="role_ids">
                                Select Roles
                            </label>
                            <div class="flex items-center">
                                <v-select
                                    id="role_ids"
                                    class="w-full"
                                    :reduce="options => options.id"
                                    :options="roles"
                                    multiple
                                    label="name"
                                    placeholder="Select"
                                    v-model="data.role_ids"
                                ></v-select>
                            </div>
                        </div>

                        <div class="w-full md:w-1/3 px-3 mb-4">
                            <label class="form-label"
                                   for="is_active">
                                Active Status
                            </label>
                            <div class="form-switch mt-2">
                                <input type="checkbox"
                                       id="is_active"
                                       class="form-check-input"
                                       :checked="data.is_active"
                                       @change="onCheckbox($event,'is_active')"/>
                            </div>
                        </div>
                    </div>

                    <div class="text-right mt-5">
                        <a href="/userManagement/user"
                           class="btn btn-outline-secondary w-24 mr-1">Cancel</a>
                        <button type="submit"
                                class="btn btn-primary w-24"
                                :disabled="btnLoading"
                                @click="onSubmit">Save
                        </button>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import FileUpload from '../FileUpload'
import flatpickr from "flatpickr";
import "flatpickr/dist/flatpickr.css"

export default {
    name: "Add",
    components: {
        FileUpload,
    },
    props: {
        roles: {
            type: Array,
            required: true
        },
        networks: {
            type: Array,
            required: true
        },
    },
    mounted() {
        flatpickr("#expiry_date", {
            dateFormat: "Y-m-d",
            // maxDate: new Date(),
            disableMobile: "true"
        })
    },
    data: function () {
        return {
            data: {
                first_name: null,
                last_name: null,
                username: null,
                email: null,
                password: null,
                mobile_no: null,
                network_id: null,
                is_active: true,
                role_ids: []
            },
            btnLoading: false
        }
    },
    methods: {
        onSubmit() {
            this.btnLoading = true;
            axios.post('/userManagement/user', this.data)
                .then(() => {
                    this.btnLoading = false;
                    this.$alertify.success('User created successfully')

                    setTimeout(() => {
                        window.location.href = '/userManagement/user'
                    }, 1000);

                }).catch((error) => {
                this.btnLoading = false;
                if (error.response.status === 422) {
                    this.$alertify.error(Object.values(error.response.data.errors)[0][0])
                }
            })
        },
        onCheckbox(e, key) {
            this.data[key] = e.target.checked;
        }
    }
}
</script>

