\n                                Select Store\n                            </label>\n                            <div class=\"flex items-center\">\n                                <v-select\n                                    id=\"store_id\"\n                                    class=\"w-full\"\n                                    :reduce=\"options => options.id\"\n                                    :options=\"stores\"\n                                    label=\"name\"\n                                    placeholder=\"Select\"\n                                    v-model=\"data.store_id\"\n                                ></v-select>\n                            </div>\n                        </div>\n\n                        <div class=\"w-full md:w-1/4 px-3 mb-4\">\n                            <label class=\"form-label\"\n                                   for=\"expiry_date\">\n                                Expiry Date\n                            </label>\n                            <div class=\"flex items-center\">\n                                <input\n                                    class=\"form-control w-full\"\n                                    id=\"expiry_date\" type=\"date\"\n                                    v-model=\"data.expiry_date\">\n                            </div>\n                        </div>\n\n                        <div class=\"w-full md:w-1/4 px-3 mb-4\">\n                            <label class=\"form-label\"\n                                   for=\"type\">\n                                Type\n                            </label>\n                            <div class=\"flex items-center\">\n                                <v-select\n                                    id=\"type\"\n                                    class=\"w-full\"\n                                    :options=\"['deal','code']\"\n                                    placeholder=\"Select\"\n                                    v-model=\"data.type\"\n                                ></v-select>\n                            </div>\n                        </div>\n\n                        <div class=\"w-full md:w-1/4 px-3 mb-4\" v-if=\"data.type == 'code'\">\n                            <label class=\"form-label\"\n                                   for=\"code\">\n                                Code\n                            </label>\n                            <div class=\"flex items-center\">\n                                <input\n                                    class=\"form-control w-full\"\n                                    id=\"code\" placeholder=\"Enter Code\" type=\"text\"\n                                    v-model=\"data.code\">\n                            </div>\n                        </div>\n\n                        <div class=\"w-full px-3 mb-4\">\n                            <label class=\"form-label\"\n                                   for=\"tracking_link\">\n                                Tracking Link\n                            </label>\n                            <div class=\"flex items-center\">\n                                <input\n                                    class=\"form-control w-full\"\n                                    id=\"tracking_link\" placeholder=\"Enter Tracking Url\" type=\"text\"\n                                    v-model=\"data.tracking_link\">\n                            </div>\n                        </div>\n\n                        <div class=\"w-full px-3 mb-4\">\n                            <label class=\"form-label\"\n                                   for=\"offer_details\">\n                                Offer Details\n                            </label>\n                            <div class=\"flex items-center\">\n                                <textarea\n                                    id=\"offer_details\" class=\"form-control w-full\"\n                                    v-model=\"data.offer_details\"\n                                    cols=\"30\" rows=\"10\"\n                                ></textarea>\n                            </div>\n                        </div>\n                    </div>\n                </div>\n\n                <div class=\"text-right mt-5\">\n                    <a href=\"/affiliate/coupon\"\n                       class=\"btn btn-outline-secondary w-24 mr-1\">Cancel</a>\n                    <button type=\"submit\"\n                            class=\"btn btn-primary w-24\"\n                            :disabled=\"btnLoading\"\n                            @click=\"onSubmit\">Save\n                    </button>\n                </div>\n            </div>\n\n            <div class=\"intro-y col-span-12 lg:col-span-4\">\n                <div class=\"w-full intro-y box p-5\">\n                    <div class=\"flex flex-wrap -mx-3 mb-6\">\n\n                        <div class=\"w-full px-3 mb-4\">\n                            <label class=\"form-label\"\n                                   for=\"is_active\">\n                                Active Status\n                            </label>\n                            <div class=\"form-switch mt-2\">\n                                <input type=\"checkbox\"\n                                       id=\"is_active\"\n                                       class=\"form-check-input\"\n                                       :checked=\"data.is_active\"\n                                       @change=\"onCheckbox($event,'is_active')\"/>\n                            </div>\n                        </div>\n\n                        <div class=\"w-full px-3 mb-4\">\n                            <label class=\"form-label\"\n                                   for=\"featured_for_home\">\n                                Feature For Home\n                            </label>\n                            <div class=\"form-switch mt-2\">\n                                <input type=\"checkbox\"\n                                       id=\"featured_for_home\"\n                                       class=\"form-check-input\"\n                                       :checked=\"data.editor_choice\"\n                                       @change=\"onCheckbox($event,'featured_for_home')\"/>\n                            </div>\n                            <small>The coupon will be added in featured for home</small>\n                        </div>\n\n                    </div>\n                </div>\n            </div>\n\n        </div>\n    </div>\n</template>\n\n<script>\nexport default {\n    name: \"Add\",\n    props: ['stores'],\n    data: function () {\n        return {\n            data: {\n                store_id: null,\n                offer_box: null,\n                offer_details: null,\n                tracking_link: null,\n                expiry_date: null,\n                type: null,\n                code: null,\n                featured_for_home: true,\n                is_active: true\n            },\n            btnLoading: false\n        }\n    },\n    watch: {\n        'data.type': function (val) {\n            if (val == 'deal') {\n                this.data.code = null\n            }\n        }\n    },\n    methods: {\n        onSubmit() {\n            this.btnLoading = true;\n            axios.post('/affiliate/coupon', this.data)\n                .then(() => {\n                    this.btnLoading = false;\n                    this.$alertify.success('Coupon created successfully')\n\n                    setTimeout(() => {\n                        window.location.href = '/affiliate/coupon'\n                    }, 1000);\n\n                }).catch((error) => {\n                this.btnLoading = false;\n                if (error.response.status === 422) {\n                    this.$alertify.error(Object.values(error.response.data.errors)[0][0])\n                }\n            })\n        },\n        onCheckbox(e, key) {\n            this.data[key] = e.target.checked;\n        }\n    }\n}\n</script>\n\n<style scoped>\n@media (min-width: 768px) {\n    .md\\:w-1\\/2 {\n        width: 25%;\n    }\n\n    .md\\:w-1\\/3 {\n        width: 33.333333%;\n    }\n\n    .md\\:w-1\\/4 {\n        width: 50%;\n    }\n\n    .md\\:w-1\\/5 {\n        width: 75%;\n    }\n}\n\n.border-radius-15 {\n    border-radius: 15px;\n}\n\n.close {\n    position: absolute;\n    right: 12px;\n    margin-top: -10px;\n}\n</style>\n"],"sourceRoot":""}]);
